.app__menu__nav:nth-child(4) {
        -webkit-animation: button-ani 0.5s ease-in-out 0.4s;
                animation: button-ani 0.5s ease-in-out 0.4s;
}
.app__menu__nav:nth-child(5) {
        -webkit-animation: button-ani 0.5s ease-in-out 0.5s;
